.grade {
  border-spacing: 0; }
  .grade__cell {
    border: 1px solid #ffffff;
    width: 100px; }
    .grade__cell--head {
      padding: 10px;
      text-align: start;
      text-transform: uppercase;
      font-weight: normal;
      font-size: 1.0em; }
    .grade__cell--body {
      padding: 20px;
      text-align: center;
      text-transform: uppercase;
      font-weight: normal;
      font-size: 3.0em;
      -webkit-transition: 0.5s ease;
      -o-transition: 0.5s ease;
      transition: 0.5s ease; }
      .grade__cell--body p {
        font-size: 0.6rem; }
    .grade__cell--body:hover {
      background: #73aa4f;
      text-shadow: none; }

@media (min-width: 320px) and (max-width: 767px) {
  .grade {
    padding: 20px; }
    .grade__cell {
      padding: 10px;
      width: 60px; }
      .grade__cell--body {
        font-size: 2.0em; }
        .grade__cell--body p {
          font-size: 0.4rem; } }



